.ImageWrapper {
  width: 100%;
  padding-top: 100%;
  position: relative;

  &:hover > .PlayButton {
    visibility: visible;
  }
  cursor: pointer;
}

.ImageWrapperActive {
  & > .PlayButton {
    visibility: visible !important;
  }
}

.Image {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 5px 8px 0 rgba(0, 0, 0, 0.14),
    0 1px 14px 0 rgba(0, 0, 0, 0.12);

  &:hover {
    -webkit-filter: brightness(0.2);
    filter: brightness(0.2);
  }
}

.ImageActive {
  -webkit-filter: brightness(0.2);
  filter: brightness(0.2);
}

.PlayButton {
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  z-index: 1;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.7);
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  transition: all 0.1s ease-in-out;
  &:hover + .Image {
    -webkit-filter: brightness(0.2);
    filter: brightness(0.2);
  }
  &:hover {
    transform: scale(1.2);
  }
}

.PlayButtonActive{
  border: 1px solid rgba(243, 77, 79,0.7);
  //transform: scale(1.5);
  //animation: sizeAnimation 4s infinite;
  //animation-timing-function: ease-in-out;
}

@keyframes sizeAnimation {
  50% {transform: scale(1.5); background-color: #000}
}
